import React, { useEffect } from 'react'
import { Link, useHistory } from 'react-router-dom'

import './HomePage.scss'
import title from '../../assets/images/title.png'
import logo from '../../assets/images/logo.png'
import Button from '../../shared/Button/Button'
import env from '../../constants/utils'

const HomePage = () => {

    const history = useHistory()


    useEffect( ()=> {
        if(!env.prod && !/rick-and-morty-quiz/.test(window.location.href)) history.push('./rick-and-morty-quiz')
    }, [])

    return (
        <div className="menu">
            <img className="menu__title" src={title} alt="title"></img>
            <img className="menu__logo" src={logo} alt="logo"></img>
            <div className="menu__controllers">
                <Link to={env.prod ? "./records" : "rick-and-morty-quiz/records"}><Button text="RECORDS" types={["menu", "secondary"]}/></Link>
                <Link to={env.prod ? "./game/settings" : "rick-and-morty-quiz/game/settings"}><Button text="NEW GAME" types={["menu"]} /></Link>
            </div>
        </div>
    )
}

export default HomePage